"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import Link from "next/link";

const StaffPage = () => {
  const router = useRouter();
  const { id } = router.query;

  const [staff, setStaff] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");

  useEffect(() => {
    async function loadStaff() {
      if (!id) return;
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/staff`);
        const json = await res.json();
        setStaff(json.data || []);
      } catch (error) {
        console.error("Erro ao carregar staff:", error);
      } finally {
        setLoading(false);
      }
    }
    loadStaff();
  }, [id]);

  // Filtrar staff por nome ou posi√ß√£o
  const filteredStaff = staff.filter(person => 
    person.person.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    person.positions?.some((pos: string) => 
      pos.toLowerCase().includes(searchTerm.toLowerCase())
    )
  );

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/detail.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        padding: "20px",
      }}
    >
      <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
        {/* Cabe√ßalho */}
        <div style={{ 
          display: "flex", 
          justifyContent: "space-between", 
          alignItems: "center", 
          marginBottom: 30,
          flexWrap: "wrap",
          gap: 15
        }}>
          <h1 style={{ color: "white", margin: 0 }}>Staff do Anime</h1>
          
          <div style={{ display: "flex", gap: 15, alignItems: "center" }}>
            {/* Busca */}
            <input
              type="text"
              placeholder="Buscar por nome ou posi√ß√£o..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              style={{
                padding: "10px 15px",
                borderRadius: "5px",
                border: "1px solid #555",
                background: "rgba(0, 0, 0, 0.7)",
                color: "white",
                width: "250px",
                outline: "none"
              }}
            />
            
            <Link href={`/${id}`}>
              <button style={{ 
                padding: "10px 20px", 
                background: "#333", 
                color: "white", 
                border: "none", 
                borderRadius: "5px", 
                cursor: "pointer",
                fontWeight: "bold"
              }}>
                ‚Ü© Voltar ao Anime
              </button>
            </Link>
          </div>
        </div>

        {/* Contador */}
        <div style={{ 
          color: "white", 
          marginBottom: 20,
          background: "rgba(0, 0, 0, 0.5)",
          padding: "10px 15px",
          borderRadius: "5px"
        }}>
          <p style={{ margin: 0 }}>
            {filteredStaff.length} membro{filteredStaff.length !== 1 ? 's' : ''} do staff encontrado{filteredStaff.length !== 1 ? 's' : ''}
            {searchTerm && ` para "${searchTerm}"`}
          </p>
        </div>

        {/* Grid de staff */}
        {loading ? (
          <p style={{ color: "white", textAlign: "center", fontSize: "18px" }}>Carregando staff...</p>
        ) : filteredStaff.length === 0 ? (
          <div style={{ 
            textAlign: "center", 
            padding: "40px",
            background: "rgba(0, 0, 0, 0.6)",
            borderRadius: "10px"
          }}>
            <p style={{ color: "white", fontSize: "18px" }}>
              {searchTerm ? `Nenhum staff encontrado para "${searchTerm}"` : "Nenhum membro do staff encontrado."}
            </p>
          </div>
        ) : (
          <div style={{ 
            display: "grid", 
            gridTemplateColumns: "repeat(auto-fill, minmax(300px, 1fr))", 
            gap: 20
          }}>
            {filteredStaff.map((person: any) => (
              <div key={person.person.mal_id} style={{ 
                background: "rgba(0, 0, 0, 0.7)", 
                borderRadius: 10, 
                padding: 20,
                backdropFilter: "blur(10px)",
                border: "1px solid rgba(255, 255, 255, 0.1)",
                transition: "transform 0.2s",
                cursor: "pointer"
              }}
              onMouseEnter={(e) => e.currentTarget.style.transform = "translateY(-5px)"}
              onMouseLeave={(e) => e.currentTarget.style.transform = "translateY(0)"}
              >
                <div style={{ display: "flex", gap: 15, alignItems: "flex-start" }}>
                  <img 
                    src={person.person.images.jpg.image_url} 
                    alt={person.person.name}
                    style={{ 
                      width: "80px", 
                      height: "100px", 
                      objectFit: "cover", 
                      borderRadius: 5,
                      border: "2px solid #1e88e5"
                    }}
                  />
                  <div style={{ flex: 1 }}>
                    <h3 style={{ color: "white", margin: "0 0 10px 0" }}>
                      {person.person.name}
                    </h3>
                    
                    <div style={{ 
                      display: "flex", 
                      flexWrap: "wrap", 
                      gap: 5,
                      marginBottom: 10
                    }}>
                      {person.positions?.map((position: string, idx: number) => (
                        <span
                          key={idx}
                          style={{
                            background: position.toLowerCase().includes("director") ? "#1e88e5" :
                                      position.toLowerCase().includes("writer") ? "#43a047" :
                                      position.toLowerCase().includes("producer") ? "#f4511e" :
                                      position.toLowerCase().includes("design") ? "#8e24aa" :
                                      "#555",
                            color: "white",
                            padding: "3px 8px",
                            borderRadius: "12px",
                            fontSize: "12px",
                            fontWeight: "bold"
                          }}
                        >
                          {position}
                        </span>
                      ))}
                    </div>
                    
                    <div style={{ color: "#aaa", fontSize: "14px" }}>
                      <p style={{ margin: "5px 0" }}>
                        <strong>Favoritos:</strong> {person.person.favorites?.toLocaleString() || "N/A"}
                      </p>
                      {person.person.website_url && (
                        <a 
                          href={person.person.website_url} 
                          target="_blank" 
                          rel="noopener noreferrer"
                          style={{ color: "#1e88e5", textDecoration: "none" }}
                        >
                          üåê Site Oficial
                        </a>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default StaffPage;